.App {
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  background: peachpuff;

}
.Wrapper{
  width: 1200px;
  height: 700px;
  background: antiquewhite;
  border: 2px solid darkblue;
  flex-wrap: wrap;

}
.Display{
  margin-bottom: 300px;
  font-size: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}
.tableInDisplay td{
  width: 600px;

}
.tableInDisplay span{
 margin-right: 100px;
  width: 300px;
}
.tableInDisplay span{
  width: 200px;
}
.Display form{
  padding-bottom: 80px;
}
.Display button{
  width: 200px;
  height: 50px;
  border-radius: 3px;
}
.WrapperButtons{
  display: flex;

}
.Controls{
  display: flex;
  flex-direction: row;
  justify-content: center;

}
.Button{
  display: flex;
}
.Button button{
  width: 200px;
  height: 50px;
  border-radius: 3px;
}
.Display input{
  height: 60px;
  border-radius: 3px;
  text-align: center;
}


.buttonColor{
  background-color: cornflowerblue;
  color: white;
}
.dispayColor{
  background-color: lightskyblue;
  color: firebrick;
  padding: 5px;
  border-radius: 5px;

}

.errorInput{
  border: 2px solid red;

}
.errorInInputs{
  font-size: 50px;
  color: red;
  height: 20px;
}

.OkInInputs{
  font-size: 50px;
  color: black;
  height: 20px;
}
